import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { DeliveryVnpostComponent } from './delivery-vnpost.component';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { Routes, RouterModule } from '@angular/router';
import { IonicModule } from '@ionic/angular';
import { TranslateModule } from '@ngx-translate/core';
import { NgZorroAntModule } from 'src/app/ng-zorro-ant.module';
import { CreateBillRetailVnpostComponent } from './create-bill-retail-vnpost/create-bill-retail-vnpost.component';
import { CreateBillCustomerVnpostComponent } from './create-bill-customer-vnpost/create-bill-customer-vnpost.component';
import { DetailBillVnpostComponent } from './detail-bill-vnpost/detail-bill-vnpost.component';
import { VnpostModule } from '../../carrier-partner/components/vnpost-component/vnpost.module';
const routes: Routes = [
  {
    path: "",
    children: [
      {
        path: "",
        component: DeliveryVnpostComponent
      },
      {
        path: "create-order-retail",
        component: CreateBillRetailVnpostComponent
      },
      {
        path: "create-order-customer",
        component: CreateBillCustomerVnpostComponent
      },
      {
        path: "detail-bill",
        component: DetailBillVnpostComponent
      }
    ]
  }

]

@NgModule({
  declarations: [DeliveryVnpostComponent, CreateBillRetailVnpostComponent, CreateBillCustomerVnpostComponent, DetailBillVnpostComponent],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    NgZorroAntModule,
    IonicModule,
    FormsModule,
    TranslateModule,
    ReactiveFormsModule,
    VnpostModule
  ]
})
export class DeliveryVnpostModule { }
