import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { VhAlgorithm, VhQuerySales, VhQuery, VhAuth } from 'ionic-vhframeworks';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { LanguageService } from 'src/app/sales/services/language.service';
import { VnpostService } from '../../services/vnpostPost/vnpost-service.service';

@Component({
  selector: 'app-delivery-vnpost',
  templateUrl: './delivery-vnpost.component.html',
  styleUrls: ['./delivery-vnpost.component.scss']
})
export class DeliveryVnpostComponent implements OnInit {

  bills: Array<any> = [];
  show_bills: any = [];
  //Hôm nay 0h- 23h59
  public date: Date[] = [new Date(), new Date()];
  dataRestore: any = this.router.getCurrentNavigation().extras.state?.dataRestore
  //biến cờ show
  status_open: boolean = false;
  data_delivery: any;
  indexSelected: number = 0;
  search: string = '';
  disabledDate = (current: Date): boolean => {
    if (current.getTime() < new Date(this.vhAuth.getStore().registrationdate).getTime() - 24 * 60 * 60 * 1000) return true
    return false
  }
  list_status: Array<any> =
    ["All", "Đã xóa", "Vào điều tin", "Hủy", "Báo Hủy", "Đã nhận báo hủy", "Vào Bưu cục chuyển phát", "Phát không thành công", "Phát thành công", "Vào hệ thống Paypost/Chờ trả tiền", "Đã trả tiền cho người gửi", "Phát hoàn không thành công", "Phát hoàn thành công"]
  constructor(
    public lang: LanguageService,
    private router: Router,
    private vhComponent: VhComponent,
    public vhAlgorithm: VhAlgorithm,
    private vnPost: VnpostService,
    private vhQuerySales: VhQuerySales,
    private vhQuery: VhQuery,
    private cdRef: ChangeDetectorRef,
    private vhAuth: VhAuth
  ) {
    this.date[0].setHours(0, 0, 0, 0)
    this.date[1].setHours(23, 59, 59, 59)
  }

  ngOnInit(): void {
    if (this.dataRestore) {
      this.search = this.dataRestore.dataRestore.search
      this.indexSelected = this.dataRestore.dataRestore.indexSelected
      this.date = [new Date(this.dataRestore.dataRestore.date[0]), new Date(this.dataRestore.dataRestore.date[1])];
      this.bills = this.dataRestore.dataRestore.bills
      this.show_bills = [...this.dataRestore.dataRestore.bills]
      this.data_delivery = this.dataRestore.data_delivery
      if (this.dataRestore.reload) this.getInvoices();
    }
  }
  goBack() {
    this.router.navigate(["/sales/dashboard/manage/delivery/shipping-bill"]);
  }
  /**
    * Hàm lấy bill trong khoăng thời gian được set
    */
  getInvoices() {
    this.bills = []
    this.status_open = false;
    this.data_delivery = this.vhQuerySales.getlocalDeliverys().filter(item => item.active && item.type == 4)[0];
    this.vhComponent.showLoading("", "transparent-loading").then(() => {
      this.vhQuerySales.getBills_byFields( { date: { $gte: new Date(this.date[0].setHours(0, 0, 0)), $lte: new Date(this.date[1].setHours(23, 59, 59, 59)) }, bill_type: { $eq: 1 }, id_branch: { $eq: this.vhQuerySales.getDefaultBranch()._id } }, {}).then((result: Array<any>) => {
        let process: Array<any> = []
        result.forEach(item => {
          if (item.id_delivery == this.data_delivery._id) {
            item.customer_name = this.vhQuerySales.getlocalCustomer(item.id_customer).name
            if (item.order_code) process.push(this.vnPost.getDetailOrderById(this.data_delivery.Token, item.order_code))
            this.bills.unshift(item)
          }
        })
        Promise.all(process).then((response: Array<any>) => {
          response.forEach(data => {
            let index = this.bills.findIndex(item => item.order_code == data.Id)
            if (index >= 0) { this.bills[index].delivery = data; this.bills[index].ItemCode = data.ItemCode }
          })
          this.show_bills = [...this.bills]
          this.vhComponent.hideLoading(0)
          this.searchBill(this.search)
        }).catch(err => { console.error(err); this.vhComponent.hideLoading(0) })
      }).catch(err => { console.error(err); this.vhComponent.hideLoading(0) })
    })
  }
  changeTime(event) {
    if (event[1].getTime() - event[0].getTime() <= 2678400000) {
    } else {
      this.vhComponent.showToast(2000, this.lang.translate("The time to view no more than 30 days"), "alert-toast")
      this.date = [new Date(event[0]), new Date(event[0].getTime() + 2678313600)]
    }
  }
  //searchbill
  searchBill(value) {
    this.show_bills = this.vhAlgorithm.searchList(
      this.vhAlgorithm.changeAlias(value).toLowerCase(),
      this.bills,
      ["bill_code", "ItemCode"]
    );
  }
  gotoCreateOrder(bill) {
    let state = { billInfo: bill, data_delivery: this.data_delivery, dataRestore: { indexSelected: this.indexSelected, search: this.search, date: this.date, bills: this.bills } }
    this.router.navigate([`/dashboard/manage/delivery/shipping-bill/delivery-vnpost/create-order-${bill.id_customer != "id_retail" ? 'customer' : 'retail'}`], { state: state });
  }
  gotoBillDetail(bill) {
    
    let state = { billInfo: bill, data_delivery: this.data_delivery, dataRestore: { indexSelected: this.indexSelected, search: this.search, date: this.date, bills: this.bills } }
    this.router.navigate(["/sales/dashboard/manage/delivery/shipping-bill/delivery-vnpost/detail-bill"], { state: state })
  }
  /**
  * filter các trạng thái của bill 
  * */
  filterStatus(value) {
    this.searchBill(this.search)
    if (value != 0) this.show_bills = [...this.show_bills].filter(item => item.delivery && item.delivery.OrderStatusId == this.getStatusOfOrder(value))
    // this.searchBill(this.search)
  }
  getStatusOfOrder(value) {
    switch (value) {
      case 1: return 10
      case 2: return 20
      case 3: return 60
      case 4: return 61
      case 5: return 62
      case 6: return 70
      case 7: return 91
      case 8: return 100
      case 9: return 110
      case 10: return 120
      case 11: return 161
    }
  }
  onCurrentPageDataChange(event) { }
  heightScroll: string;
  ngAfterViewChecked() {
    if (document.getElementById("delivery-vnpost") && document.querySelector(".delivery-vnpost-header") && document.querySelector(".ant-table-pagination")) {
      this.heightScroll = document.getElementById("delivery-vnpost").clientHeight - document.querySelector(".delivery-vnpost-header").clientHeight -
        document.querySelector(".ant-table-pagination").clientHeight - 135 + "px";
    }
    this.cdRef.detectChanges();
  }

}
