import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { VhQuerySales, VhAlgorithm } from 'ionic-vhframeworks';
import { NzModalService } from 'ng-zorro-antd/modal';
import { DeliveryA45Component } from 'src/app/sales/components/print/delivery/A45/delivery-a45/delivery-a45.component';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { VhSaleInvoices } from 'src/app/sales/interface/vh-sale-invoice';
import { LanguageService } from 'src/app/sales/services/language.service';
import { VnpostService } from '../../../services/vnpostPost/vnpost-service.service';

@Component({
  selector: 'app-create-bill-customer-vnpost',
  templateUrl: './create-bill-customer-vnpost.component.html',
  styleUrls: ['./create-bill-customer-vnpost.component.scss']
})
export class CreateBillCustomerVnpostComponent implements OnInit {

  dataRestore: any = this.router.getCurrentNavigation().extras.state
  billDetails: Array<any> = []
  addInformation: FormGroup
  deliveryInfo: FormGroup
  productInfo: FormGroup
  data_delivery: any
  data_delivery_recei: any = {}
  service: any
  serviceSelected: any = {}
  CodAmountEvaluation: any
  OrderAmountEvaluation: any
  invoice: VhSaleInvoices
  listProvinceVnpost: Array<any> = []
  listDistrictVnpost: Array<any> = []
  listWardVnpost: Array<any> = []
  listServiceVnpost: Array<any> = []
  isLoading: boolean = false
  dataAddress: any = { TenTinhThanh: '', TenQuanHuyen: '', TenPhuongXa: '' }
  heightScroll: any = '100px'
  constructor(
    private router: Router,
    private vhComponent: VhComponent,
    private vhQuerySales: VhQuerySales,
    private lang: LanguageService,
    public vhAlgorithm: VhAlgorithm,
    private vnPost: VnpostService,
    private cdRef: ChangeDetectorRef,
    private modalService: NzModalService
  ) {
  }

  ngOnInit() {
    this.initForm();
  }
  initForm() {
    let customer = this.vhQuerySales.getlocalCustomer(this.dataRestore.billInfo.id_customer)
    this.addInformation = new FormGroup({
      ReceiverFullname: new FormControl(customer.name, [Validators.required, Validators.minLength(6)]),
      ReceiverTel: new FormControl(customer.phone, [Validators.required, Validators.minLength(10), Validators.maxLength(10)]),
      ReceiverAddress: new FormControl('', Validators.required),
      ReceiverProvinceId: new FormControl('', Validators.required),
      ReceiverDistrictId: new FormControl('', Validators.required),
      ReceiverWardId: new FormControl('', Validators.required),
    })
    this.deliveryInfo = new FormGroup({
      ServiceName: new FormControl('', Validators.required),
      CustomerNote: new FormControl(''),
      ReceiverAddressType: new FormControl(0),
      IsReceiverPayFreight: new FormControl(false),
      CodAmountEvaluation: new FormControl(0),
      OrderAmountEvaluation: new FormControl(0),
      IsPackageViewable: new FormControl(true),
    })
    this.productInfo = new FormGroup({
      WeightEvaluation: new FormControl('', [Validators.required, Validators.min(0.0000000000000000000001), Validators.max(1000000)]),
      WidthEvaluation: new FormControl('', [Validators.required, Validators.max(100)]),
      LengthEvaluation: new FormControl('', [Validators.required, Validators.max(100)]),
      HeightEvaluation: new FormControl('', [Validators.required, Validators.max(100)]),
    })
    this.getData();
  }
  ngAfterViewInit() {
    this.vhAlgorithm.waitingStack().then(() => {
      this.CodAmountEvaluation = this.vhAlgorithm.vhnumeral('.CodAmountEvaluation');
      this.OrderAmountEvaluation = this.vhAlgorithm.vhnumeral('.OrderAmountEvaluation');
    });
  }

  ngAfterViewChecked() {
    if (document.querySelector('productInfo'))
      this.heightScroll = document.querySelector('productInfo').clientHeight - 156 + 'px'

    this.cdRef.detectChanges()
  }
  //lấy dữ liệu billDetail
  getData() {
    this.vhComponent.showLoading("", "transparent-loading").then(() => {
      Promise.all([
        this.vhQuerySales.refreshLocalAddresss(),
        this.vhQuerySales.refreshLocalDeliverys(),
      ]).then(() => {
        this.vhQuerySales.getBill_details_byId_bill(this.dataRestore.billInfo._id).then((result: Array<any>) => {
          this.vhComponent.hideLoading(0)
          this.billDetails = result.map(item => {
            let name_product = this.vhQuerySales.getlocalDetailProduct(item.id_subproduct ? item.id_subproduct : item.id_product).name;
            return { ...item, name_product: name_product, }
          })
          this.invoice = new VhSaleInvoices(this.dataRestore.billInfo, this.billDetails, this.vhQuerySales);
          //lấy địa chỉ lấy hàng
          let listAddresss: Array<any> = this.vhQuerySales.getlocalAddresss().filter(item => item.id_delivery == this.dataRestore.data_delivery._id && item.type == 5);
          this.data_delivery = listAddresss.sort((a, b) => Date.parse(b.date) - Date.parse(a.date))[0]
          //lấy địa chỉ giao hàng
          let listAddresssRecei: Array<any> = this.vhQuerySales.getlocalAddresss().filter(item => item.id_delivery == this.dataRestore.data_delivery._id && item.type == 6 && item.id_customer == this.dataRestore.billInfo.id_customer);
          this.data_delivery_recei = listAddresssRecei.sort((a, b) => Date.parse(b.date) - Date.parse(a.date))[0]
          this.data_delivery_recei && this.attatchDataAddressToForm(this.data_delivery_recei)


          let printer = this.vhQuerySales.getLocalPrintPage('page_desktop_sales')
        if (printer) this.checkPrinter = printer.print_sizes[printer.print_sizes.findIndex(e => e._id == printer.print_size_default)].enable;
        }).catch(err => { console.error(err); this.vhComponent.hideLoading(0) })
      })

    })
  }
  checkPrinter: boolean = false;
  goBack(success?) {
    this.router.navigate(["/sales/dashboard/manage/delivery/shipping-bill/delivery-vnpost"], {
      state: { dataRestore: this.dataRestore }
    })
  }
  /**
   * Hàm này để chuyển đưa dữ liệu vào form nếu có
   * @param receive 
   */
  attatchDataAddressToForm(receive: any) {
    this.addInformation.controls.ReceiverAddress.setValue(receive.ReceiverAddress)
    this.addInformation.controls.ReceiverProvinceId.setValue(receive.ReceiverProvinceId)
    this.addInformation.controls.ReceiverDistrictId.setValue(receive.ReceiverDistrictId)
    this.addInformation.controls.ReceiverWardId.setValue(receive.ReceiverWardId)
  }
  createOrder(print?) {
    this.modalService.confirm({
      nzTitle: this.lang.translate("Do you want to create order ?"),
      nzCancelText: this.lang.translate("Cancel"),
      nzOnOk: () => {
        return new Promise<any>((resolve) => {
          let data_deli = { ...this.deliveryInfo.value, CodAmountEvaluation: this.CodAmountEvaluation.getRawValue(), OrderAmountEvaluation: this.OrderAmountEvaluation.getRawValue() }
          let delivery = this.vhQuerySales.getlocalDelivery(this.data_delivery.id_delivery)
          this.vnPost.createOrderVnpost(delivery.Token, this.vnPost.renderFormatCreateOrder(this.data_delivery, this.addInformation.value, this.productInfo.value, data_deli, delivery.CustomerCode, this.dataRestore.billInfo.bill_code)).then((response: any) => {
            if (response.Id) {
              this.invoice.setOrderCode(response.Id)
              this.vhQuerySales.updateBill_Billdetail(this.invoice.getID(), this.invoice.getCreateUpdateInvoice(1), this.invoice.getCreateUpdateInvoiceDetail()).then((data: any) => {
                resolve(true)
                this.dataRestore.reload = true
                this.vhComponent.alertMessageDesktop('success', this.lang.translate('Create order successfully !'))
                if (print) this.showPrint(response)
                else
                  this.goBack()
              })
            }

          }).catch(err => { this.vhComponent.alertMessageDesktop('error', err) })

        })
      },
      nzOnCancel: () => { },
    })
  }
   /**
   * lấy thông tin dữ liệu trang in
   * @returns Promise<any>
   * @example this.checkPrint()
   */
  checkPrint() {
    return new Promise((resolve, rejects) => {
      let printer = this.vhQuerySales.getLocalPrintPage('page_desktop_sales')
      if (printer && printer.print_sizes[printer.print_sizes.findIndex(e => e._id == printer.print_size_default)].enable) resolve(printer.print_sizes[printer.print_sizes.findIndex(e => e._id == printer.print_size_default)])
      else rejects("No")
    })
  }

  showPrint(res) {
    this.checkPrint().then((printer) => {
      this.vhComponent.showModal(this.renderPrintPage(printer['_id']), {
        billInfo: {
          ...this.dataRestore.billInfo, data: this.vnPost.renderFormatPrintOrder(
            { ...res, ReceiverFullAddress: `${this.addInformation.value.ReceiverAddress}, ${this.data_delivery_recei.TenPhuongXa},  ${this.data_delivery_recei.TenQuanHuyen},  ${this.data_delivery_recei.TenQuanHuyen}` }), order_code: res.ItemCode
        },
        billDetails: this.billDetails,
        data_delivery: { ...this.data_delivery, type: 4 },
        printer 
      }, false, false, `modal-print-${printer['type']}`).then(modal => {
        modal.onWillDismiss().then(() => {
          this.goBack(true);
        })
      })

    }, (err) => {
      this.goBack(true)
    })
  }

  /**
   * trả về component của trang in tương ứng
   * @example this.renderPrintPage('print_size_a4')
   * @returns component
   */
  private renderPrintPage(type: 'K57' | 'K80' | 'A4') {
    switch (type) {
      default: return DeliveryA45Component
    }
  }
  //------------------------------------Xã huyện tỉnh------------------------------------

  getAllService(change?) {
    if (this.addInformation.valid && this.productInfo.valid) {
      let data_deli = { ...this.deliveryInfo.value, CodAmountEvaluation: this.CodAmountEvaluation.getRawValue(), OrderAmountEvaluation: this.OrderAmountEvaluation.getRawValue() }
      this.listServiceVnpost = []
      this.isLoading = true
      let delivery = this.vhQuerySales.getlocalDelivery(this.data_delivery.id_delivery)
      this.vnPost.getServiceNameVnpost(delivery.Token, this.vnPost.renderFormatService(this.data_delivery, this.addInformation.value, this.productInfo.value, data_deli, delivery.CustomerCode)).then((result: Array<any>) => {
        result.forEach(item => {
          if (item.Success) {
            item.TenDichVu = this.renderNameService(item.MaDichVu, item.TongCuocBaoGomDVCT)
            this.listServiceVnpost.push(item)
            change && this.changeService(change)
          }
        })
        this.isLoading = false
      })
    }
  }

  changeService(event) {
    this.serviceSelected = this.listServiceVnpost.find(item => item.MaDichVu == event)
  }
  renderNameService(MaDichVu, TongCuocBaoGomDVCT) {
    switch (MaDichVu) {
      case 'EMS': return `${this.lang.translate('Chuyển phát nhanh')} - ${this.vhAlgorithm.vhcurrencyunit(TongCuocBaoGomDVCT)}`
      case 'BK': return `${this.lang.translate('Chuyển phát thường')} - ${this.vhAlgorithm.vhcurrencyunit(TongCuocBaoGomDVCT)}`
      case 'TMDT_EMS': return `TMDT_EMS - ${this.lang.translate('TMĐT-Chuyển phát nhanh EMS ')} - ${this.vhAlgorithm.vhcurrencyunit(TongCuocBaoGomDVCT)}`
      case 'TMDT_BK': return `TMDT_BK - ${this.lang.translate('TMĐT-Chuyển phát tiêu chuẩn')} - ${this.vhAlgorithm.vhcurrencyunit(TongCuocBaoGomDVCT)}`
      case 'TMDT_EMS_TK': return `TMDT_EMS_TK - ${this.lang.translate('TMĐT-Chuyển phát nhanh EMS tiết kiệm (liên vùng)')} - ${this.vhAlgorithm.vhcurrencyunit(TongCuocBaoGomDVCT)}`
    }
  }
  /**Thay đổi tiền thu hộ */
  changeCodAmountEvaluation() {
    (this.serviceSelected.MaDichVu && this.deliveryInfo.value.IsReceiverPayFreight) && this.getAllService(this.serviceSelected.MaDichVu)
  }
  /**Thay đổi người nhận hay người gửi trả cước */
  changeData() {
    this.serviceSelected.MaDichVu && this.getAllService(this.serviceSelected.MaDichVu)
  }

  // ----------modal list address pickup---------
  pickupAdresssVnpost: boolean = false;
  dataPickupAddressVnpost: any;
  cancelListVnpost(event) {
    if (event && event._id) {
      this.attatchDataAddressToForm(event)
      this.data_delivery_recei = event;
      this.changeData()
    }
    else if (event && !event._id) {
      this.data_delivery_recei = {}
      // this.data_delivery_recei.id_delivery = '';
      this.addInformation.controls.ReceiverWardId.setValue('')
    }
    this.pickupAdresssVnpost = false;
  }

}
